import React, { useEffect, useState } from 'react';
import RouterLink from './ui/RouterLink';
import Icon from './ui/Icon';
import sidebarSlice from '../store/slices/sidebar.slice';
import { useDispatch } from 'react-redux';
import routes from '../constants/routes.constant';
import { useLocation } from 'react-router';

interface Props {

}

const  NavBar : React.FC<Props>  = () =>  {
    // VARIABLES
    const dispath = useDispatch();
    const location = useLocation()
    const [ activeRoute, setActiveRoute ] = useState(routes.home);

    // HOOKS
    useEffect( () => {
        setActiveRoute(getActiveRoute(location.pathname));
    }, [location]);

    // METHODS
    const closeNavOnly = () => {
        return ( 
            activeRoute?.id === 'legal-notices' ||
            activeRoute?.id === 'my-companion'
        )
    }

    const getActiveRoute = (path: string) => {
        const routeKeys = Object.keys(routes);
        const clonedRoutes = JSON.parse(JSON.stringify(routes));
        let activeRouteKey = routeKeys.filter( (k: string) => clonedRoutes[k].path === path)[0];
        return clonedRoutes[activeRouteKey];
    }
    

    return (
        <div className="navbar">

            <RouterLink route={routes.home} className="navbar__logo">
                <Icon name="logo" type="image"/>
            </RouterLink>

            <h1 className="navbar__title">
                {activeRoute?.label}
            </h1>

            <div className="navbar__navigation">

                {!closeNavOnly() && (
                        <>
                            <RouterLink>
                                <Icon name="dropbox"/>
                            </RouterLink>
                            
                            <RouterLink>
                                <Icon name="flip"/>
                            </RouterLink>

                            <span className="spacer"></span>

                            <RouterLink click={() => dispath(sidebarSlice.actions.open())}>
                                <Icon name="hamburger"/>
                            </RouterLink>
                        </>
                )}
                

                {
                    closeNavOnly() &&
                    <RouterLink route={routes.home}>
                        <Icon name="close-gray"/>
                    </RouterLink>
                }
                

            </div>

        </div>   
    )
}

export default NavBar;