import React, { useState } from 'react';
import { useHistory } from 'react-router';
import Icon from '../../../components/ui/Icon';
import routes from '../../../constants/routes.constant';

interface Props {
    onClose: () => void
}

interface iChoice {
    label: string,
    value: number
}

const AgeCategory : React.FC<Props>  = ({
    onClose
}) =>  {
    // VARIABLES
    const history = useHistory();
    const choices: iChoice[] = [
        { label: 'Kids', value: 1 },
        { label: 'Young Adult', value: 2 },
        { label: 'Adult', value: 3 }
    ];
    const [choiced, setChoiced ] = useState<iChoice>();

    // HOOKS

    // METHODS
    const activeChoiceID = (choice: iChoice) => {
        if(!choiced)return "";
        return choiced.value === choice.value ? 'age-category__choice--active' : '';
    }

    const onSelectCategory = (choice: iChoice) => {
        setChoiced(choice);
        history.push(routes.lensProposalBuilder.path);
    }

    return (
        <div className="age-category">
            <div className="age-category__head">
                <h2 className="age-category__head-text">What’s your age category ?</h2>

                <Icon click={onClose} name="close-white"/>
            </div>
            <div className="age-category__selection">
                { choices.map((choice, index) => (
                    <div 
                        key={index}
                        className={`age-category__choice  ${activeChoiceID(choice)}`}
                        onClick={() => onSelectCategory(choice)}>
                        <label>{choice.label}</label>
                    </div>
                )) }
            </div>
        </div>
    )
}

export default AgeCategory;