import React from 'react';

interface Props {
    type: 'right' | 'left' | 'up' | 'down',
    size?: string,
    color?: string,
}

const Arrow : React.FC<Props>  = ({
    type,
    color = 'rgba(90,102,117, 0.4)',
    size = '2px'
}) =>  {
    // VARIABLES
    const stlye = {
        border: 'solid ' + color,
        borderWidth: `0 ${size} ${size} 0`,
        padding: size
    }

    // HOOKS

    // METHODS

    return (
        <div className={`arrow arrow--${type}`} style={stlye}/>
    )
}

export default Arrow;