import React, { useState } from 'react';
import Icon from '../../components/ui/Icon';
import Button from '../../components/ui/Button';
import StartFrom from './components/StartFrom';
import AgeCategory from './components/AgeCategory';
import ContinueWithID from './components/ContinueWithID';
import Modal from '../../components/Modal';
import InputText from '../../components/ui/InputText';

interface Props {

}

const HomePage : React.FC<Props>  = () =>  {
    // VARIABLES
    const [isStart, setStart] = useState(false);
    const [startFromID, setStartFromID] = useState(false);
    const [id, setId] = useState('');


    //TEMP
    const withID = false;

    const cards = [
        { icon: 'mic', label: 'What\'s new' },
        { icon: 'emat', label: 'eMat' },
        { icon: 'catalog', label: 'My Catalog' }
    ];

    // HOOKS

    // METHODS
    const onSelectStart = (type: string) => {
        setStartFromID(type==='id');
    }

    const modalOnClose = () => {
        setId('');
        setStartFromID(false);
    }

    const idOnchange = (event: any) => {
        setId(event.target.value);
    }

    return (
        <div className="home page">
            {cards.map((card,index) => (
                <div className="home__card" key={index}>                
                    <Icon name={card.icon}/>
                    <label>{card.label}</label>
                </div>
            ))}

            <div className="home__main">
                <div className="home__content">
                    <Icon className="home__eyeglass" name="eyeglass"/>
                    <h1 className="home__banner">
                        Lens Proposal Builder
                    </h1>
                    
                    {   
                        !isStart && !withID &&
                        <Button 
                        click={() => setStart(true)}
                        icon="right-arrow-circle"
                        text="Start" 
                        type="secondary"/>
                    }
                </div>

                { withID && <ContinueWithID/> }
                
                { !isStart ?
                    <StartFrom onSelect={(type) => onSelectStart(type)}/> :
                    <AgeCategory onClose={() => setStart(false)}/>
                }
            </div>

            <Modal show={startFromID} onClose={() => modalOnClose()}>
                <div className="home__start-from-id-popup">
                    <h1>Start from an ID</h1>
                    
                    <div>
                        <InputText 
                            value={id}
                            placeholder="Enter an ID" 
                            maxlength={6} 
                            onChange={idOnchange}/>
                        
                        { id && <Button text="Next" icon="right-arrow-circle-white"/> }
                        
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default HomePage;