import React from 'react';
import { useHistory } from 'react-router';
import Button from '../../components/ui/Button';
import routes from '../../constants/routes.constant';
import useBEM from '../../utils/hooks/useBEM';

interface Props {

}

const PackageOffer : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('package-offer');
    const history = useHistory();

    // HOOKS

    // METHODS
    const back = () => {
        history.push(routes.lensConsultation.path);
    }

    return (
        <div className={B() + ' page'}>
            
            <div className={E('buttons')}>
                <Button 
                    click={back}
                    text="Back" 
                    icon="left-arrow-circle-gray" 
                    iconPosition="left" type="secondary" outlined={true}/>
                    
                <Button text="Save" icon="check"/>
            </div>


            <div className={E('packages')}>
                <div className={E('package')}>
                    Package 1
                </div>
                <div className={E('package')}>
                    Package 2
                </div>
                <div className={E('package')}>
                    Package 3
                </div>                    
            </div>

        </div>
    )
}

export default PackageOffer;